.Image {
    position: relative;
    display: table;
}

.Image-Wide {
    width: 100%;
    padding-bottom: 9 / 16 * 100%;
}

.Image-Square {
    width: 100%;
    padding-bottom: 100%;
}

.Image-Pic {
    position: absolute;
    height: 100%;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    background-color: white;
}



